"use client";

import { useState } from "react";
import { registerUser, loginUser } from "../services/api"; 

export default function Menu() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const [showRegisterPopup, setShowRegisterPopup] = useState(false); // Estado para controlar o pop-up

  const handleRegister = async (username: string, password: string) => {
    try {
      const data = await registerUser(username, password);
      setMessage(`User registered successfully: ${data.username}`);
      setShowRegisterPopup(false); // Fecha o pop-up apÃ³s o registro
    } catch (error) {
      setMessage("Error registering user.");
      console.error(error);
    }
  };

  const handleLogin = async () => {
    try {
      const data = await loginUser(username, password);
      setMessage(`User logged in successfully: ${data.username}`);
    } catch (error) {
      setMessage("Error logging in.");
      console.error(error);
    }
  };

  return (
    <div className="min-h-screen bg-gray-200 text-gray-800 dark:bg-gray-800 dark:text-gray-200 flex items-center justify-center">
      <div className="bg-gray-300 dark:bg-gray-700 p-6 rounded-lg shadow-md w-96">
        <h1 className="text-xl font-bold mb-4">Menu</h1>
        <input
          type="text"
          placeholder="Username"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          className="w-full p-2 mb-4 border border-gray-400 dark:border-gray-600 rounded-lg"
        />
        <input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className="w-full p-2 mb-4 border border-gray-400 dark:border-gray-600 rounded-lg"
        />
        <div className="flex gap-4">
          <button
            onClick={() => setShowRegisterPopup(true)} // Abre o pop-up
            className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700"
          >
            Register
          </button>
          <button
            onClick={handleLogin}
            className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700"
          >
            Login
          </button>
        </div>
        {message && <p className="mt-4 text-center">{message}</p>}
      </div>

      {/* Pop-up de registro */}
      {showRegisterPopup && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
          <div className="bg-white dark:bg-gray-700 p-6 rounded-lg shadow-md w-96">
            <h2 className="text-xl font-bold mb-4">Register</h2>
            <input
              type="text"
              placeholder="Username"
              onChange={(e) => setUsername(e.target.value)}
              className="w-full p-2 mb-4 border border-gray-400 dark:border-gray-600 rounded-lg"
            />
            <input
              type="password"
              placeholder="Password"
              onChange={(e) => setPassword(e.target.value)}
              className="w-full p-2 mb-4 border border-gray-400 dark:border-gray-600 rounded-lg"
            />
            <div className="flex gap-4">
              <button
                onClick={() => handleRegister(username, password)}
                className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700"
              >
                Register
              </button>
              <button
                onClick={() => setShowRegisterPopup(false)} // Fecha o pop-up
                className="bg-red-600 text-white px-4 py-2 rounded-lg hover:bg-red-700"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

